.pentagon-container {
   width: 250px;
   height: 250px;
   position: relative;
   overflow: hidden;
 }
 .pentagon {
   width: 100%;
   height: 100%;
 }
 .inner-image {
   position: absolute;
   top: 50%;
   left: 50%;
   transform: translate(-50%, -50%);
   transition: transform 0.3s ease;
 }
 .pentagon-container:hover .inner-image {
   transform: translate(-50%, -50%) scale(1.2);
 }